:root {
	--darkGray: #1B262C;
	--blueDark: #0F4C75;
	--blueLight: #3282B8;
	--blueLighter: #BBE1FA;
	--offWhite: #E4D9D3;
}

@keyframes wavy {
    0% {
        transform: translateX(0) translateZ(0) scaleY(1)
    }
    50% {
        transform: translateX(-25%) translateZ(0) scaleY(0.55)
    }
    100% {
        transform: translateX(-50%) translateZ(0) scaleY(1)
    }
}

* {
	font-family: 'Montserrat', sans-serif;
}

body {
	background-color: #222;
	color: white;
}

.header {
	padding-top: 25px;
	margin-bottom: 75px;
}

.waveImg {
	background-repeat: repeat no-repeat;
	transform-origin: center top;
	width: 400%;
	height: 100%;
	background-position: 0 top;
	position: absolute;
	left: 0;
	background-size: 50% 100px;
}

.wave1img {
	animation: wavy 10s linear infinite;
}

.wave2img {
	animation: wavy 15s cubic-bezier(.5,.35,.5,.75) infinite;
}

.wave {
	position: absolute;
	left: 0;
	right: 0;
	width: 100%;
	height: 200px;
	overflow: hidden;
}

.wave1 {
	top: 115px;
	z-index: -5;
}

.wave2 {
	top: 145px;
	z-index: -10;
}

.solidClr {
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
}

.solidBlue {
	background-color: var(--blueLight);
	height: 115px;
	z-index: -5;
}

.solidDarkBlue {
	background-color: var(--blueDark);
	height: 145px;
	z-index: -7;
}

h1 {
	color: #E4D9D3;
	color: var(--offWhite);
	font-family: 'Montserrat Subrayada';
	text-align: center;
	display: block;
	font-size: 3.5em;
	margin: 0;
}

h2 {
	margin-top: 15px;
}

a.nav-link.active {
	opacity: 1;
}

a.nav-link:hover {
	opacity: 1;
}

a.nav-link {
	color: white;
	opacity: 0.5;
}

.image-box {
	background-color: #0F4C75;
	background-color: var(--blueDark);
	height: 100%;
	overflow: hidden;
	border-radius: 5px;
	display: flex;
	align-items: center;
}

.image-box * {
	border-radius: 5px;
}

.image-box-description {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: rgba(0, 0, 0, 0.75);
	color: #fff;
	margin: 0 15px;
	display: flex;
	justify-content: center;
	align-items: center;
	opacity: 0;
	visibility: hidden;
	text-align: center;
	transition: opacity .2s, visibility .2s;
}

.image-box-description:link,
.image-box-description:hover,
.image-box-description:active {
	color: white;
}

.image-box-description:visited {
	color: var(--offWhite);
}

.image-box:hover .image-box-description {
	visibility: visible;
	opacity: 1;
}

.carousel-item img {
	height: 200px;
	object-fit: scale-down;
}

div.carousel {
	margin-bottom: 15px;
}

ul {
	list-style-position: inside;
}

@media (min-width: 576px) { 
	h1 {
		font-size: 5em;
	}

	.carousel-item img {
		height: 300px;
	}

	.waveImg {
		width: 200%;
	}

	.wave1 {
		top: 135px;
	}

	.wave2 {
		top: 165px;
	}

	.solidBlue {
		height: 135px;
	}

	.solidDarkBlue {
		height: 165px;
	}
}

@media (min-width: 768px) { 
	h1 {
		font-size: 6.5em;
	}

	.carousel-item img {
		height: 350px;
	}
}

@media (min-width: 992px) { 
	.carousel-item img {
		height: 400px;
	}
}